body {
  margin: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch:after {
  content: "";
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite minute;
  position: absolute;
  bottom: 50%;
  left: 48%;
}

.minute-hand--speed-up {
  animation: 600s steps(60, end) infinite minute;
}

@keyframes minute {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite second;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.second-hand--speed-up {
  animation: 10s linear infinite second;
}

@keyframes second {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

